import React from 'react'
import { Link } from "react-router-dom";

const Home = () => {
    return(
        <div className="home">
            <ul>
                <li><Link to="/koronaRuletka/ruletka">Ruletka</Link></li>
                <li><Link to="/koronaRuletka/twitter">Twitter</Link></li>
            </ul>
        </div>
    )
}

export default Home